import VisualComponentDOM from "jx/comps/visualcomponent/VisualComponentDOM";
export default class Container extends VisualComponentDOM {
    constructor(dataObject) {
        super(Object.assign({}, dataObject));
        // console.log("Container", this.dataObject)
    }
    static initialiseMod(mod) {
        mod.extendsFrom("VisualComponent");
        mod.mixin(["_create", "addChild", "hasChild", "mouseChildren", "removeAllChildren", "removeChild", "getChildById", "getChildAt",
            "width", "height"], ContainerDOM, "Container");
        mod._initialise();
    }
    _create() {
        super._create();
        this.children = [];
        this.view = this.dataObject.view || $('<div/>');
        //this.view.css("position", "absolute");
    }
    set width(value) {
        console.log(this.width);
        var ratio = value / this.width;
        for (var i = 0; i < this.children.length; i++) {
            this.children[i].width *= ratio;
            this.children[i].x *= ratio;
        }
        ;
    }
    get width() {
        var width = 0;
        for (var i = 0; i < this.children.length; i++) {
            width += this.children[i].width;
        }
        ;
        return width;
    }
    set height(value) {
        console.log(this.height);
        var ratio = value / this.height;
        for (var i = 0; i < this.children.length; i++) {
            this.children[i].height *= ratio;
            this.children[i].y *= ratio;
        }
        ;
    }
    get height() {
        var height = 0;
        for (var i = 0; i < this.children.length; i++) {
            height += this.children[i].height;
        }
        ;
        return height;
    }
    set mouseChildren(bool) {
        this.view.mouseChildren = bool;
    }
    get mouseChildren() {
        return this.view.mouseChildren;
    }
    addChild(child) {
        var childElement = (child.isJXComponent) ? child : this.cc(child);
        if (childElement.render != "DOM") {
            console.error("ContainerDOM", this.id, " can not add child ", childElement.id, "with render", childElement.render);
            return null;
        }
        this.children.push(childElement);
        childElement.parent = this;
        // // get highest z-index
        // var children = this.view.children();
        // var maxZ = 0;
        // for (var i = 0; i < children.length; i++) {
        //     maxZ = Math.max(maxZ, $(children[i]).css("z-index"));
        // };
        this.view.append(childElement.view);
        //childElement.view.css("z-index", maxZ + 1);
        return childElement;
    }
    hasChild(child) {
        return this.children.indexOf(child) != -1;
    }
    removeChild(child) {
        //console.log("removeChild", child)
        if (this.view.has(child.view).length > 0) {
            //console.log("removeChild2")
            child.view.remove();
            this.children.splice(this.children.indexOf(child), 1);
        }
        ;
        //this._selected(0);
    }
    removeAllChildren() {
        for (var i = 0; i < this.children.length; i++) {
            if (this.view.has(this.children[i].view).length > 0) {
                this.children[i].view.remove();
            }
            ;
        }
        ;
        this.children.length = 0;
    }
    getChildAt(index) {
        return this.children[index];
    }
    getChildById(id) {
        for (var i = 0; i < this.children.length; i++) {
            if (this.children[i].id == id) {
                return this.children[i];
            }
        }
        ;
        return null;
    }
}
